.question-card-container {
  background-color: #fff;
  color: var(--main-color);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  width: 40vw;
  padding: 2vh 2vw;
  margin: 2vh 0;
  border-radius: 5px;
}
.question-card {
  font-size: 2rem;
  cursor: pointer;
}
.add {
  color: var(--black);
}

.quesion-info {
  display: inline-block;
  /* flex-direction: column; */
  /* justify-content: flex-start; */
  /* align-items: flex-start; */
  padding: 0 2vw;
}
.question-title {
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: var(--black);
}
.quesion-answer {
  color: var(--black);
  font-size: 1rem;
  display: block;
  height: 0vh;
  opacity: 0;
  /* transform: scaleY(0);
  transform-origin: top; */
  transition: all 0.3s ease-in;
}
.showen {
  margin-top: 4vh;
  height: 8vh;
  opacity: 1;
  /* transform: scaleY(1);
  transform-origin: top; */

  transition: all 0.3s ease-in;
}
@media screen and (max-width: 1100px) {
  .question-card-container {
    width: 60vw;
    margin: 1vh 0;
  }
}
@media screen and (max-width: 700px) {
  .question-card-container {
    width: 80vw;
    margin: 1vh 0;
  }
  .showen {
    height: 10vh;
  }
}
@media screen and (max-width: 600px) {
  .question-card-container {
    width: 80vw;
    margin: 1vh 0;
  }
  .showen {
    height: 12vh;
  }
}
